import Vue from 'vue'
import App from './AppMain.vue'
import router from './router'
import { createPinia, PiniaVuePlugin } from 'pinia'
import VueCompositionAPI from '@vue/composition-api'
import vuetify from './plugins/vuetify'
import Finder from './components2/Finder.vue';

Vue.use(PiniaVuePlugin)
const pinia = createPinia()
Vue.use(VueCompositionAPI)

Vue.config.productionTip = true;
const prodaction = true;
const baseUrl = (prodaction) ? "" : "http://localhost:5000/";

let openMap = new Map();
openMap.set("yyy",
  {
    Control: Finder,
    Params: "1445",
    SQLParams: null,
    data: {}
  });

let mainObj = {
  message: "ого",
  drawer: false,
  current: "yyy",
  openAlert: false,
  alert: function (title, text) {
    this.alertConfirm = false;
    this.alertTitle = title;
    this.alertText = text;
    this.openAlert = true;
  },
  confirm: function (title, text, action) {
    this.alertConfirm = true;
    this.alertTitle = title;
    this.alertText = text;
    this.confirmAction = action;
    this.openAlert = true;
  },
  history: ["yyy"],
  curhistory: 0,
  gridHeight: function () {
    return document.documentElement.clientHeight - 115;

  },
  selectedColor: "LightGreen",
  dateformat: function (d, f) {
    if (!d) return d;

    if (d.length != 24) {
      let res = f.match(/0\.(0+)/);

      let n = 0;
      if (res)
        if (res.length > 1) {
          n = res[1].length;
        }

      if (n > 0) return Number(d.toString()).toFixed(n);
      else return d;
    }
    f = f.replace("yyyy", d.substr(0, 4));
    f = f.replace("yy", d.substr(2, 2));
    f = f.replace("MM", d.substr(5, 2));
    f = f.replace("dd", d.substr(8, 2));
    f = f.replace("HH", d.substr(11, 2));
    f = f.replace("mm", d.substr(14, 2));
    return f;
  },

  resize: function () {
    openMap.forEach((value) => {
      if (value.resize)
        value.resize()
    });
  }


};




let openIDs = [];
openIDs.push("yyy");


Vue.component('uni-comp', {
  data: function () {
    return {
      openMap: openMap,
      mainObj: mainObj
    };
  },
  props: {
    id: {
      type: String,
      required: true
    }
  },
  render: function (createElement) {
    let C = openMap.get(this.id).Control;
    let params = openMap.get(this.id).Params;
    let v = (mainObj.current == this.id);
    return createElement(C, {
      props: {
        id: this.id,
        params: params,
        visible: v
      }
    });
  }
});


//размеры окон
window.addEventListener('resize', function() {
  if (mainObj.resize)
      mainObj.resize()
}, true);

new Vue({
  router,
  pinia,
  vuetify,
  render: h => h(App)
}).$mount('#app')

export { openMap, mainObj, openIDs, prodaction, baseUrl }