import Vue from 'vue'
import VueRouter from 'vue-router'



Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: () => import('../AppPortal.vue'),
    children:
      [
        {
        path: '',
        component: () => import('../components/Account.vue'),
        children:
        [
          { path: '', component: () => import('../components/UserCust.vue') },
          {
            path: 'account',
            component: () => import('../components/Account.vue'),
            children: [
              { path: '', component: () => import('../components/UserCust.vue') },
              { path: 'user', component: () => import('../components/User.vue') },
              { path: 'company', component: () => import('../components/Dogovor.vue') },
              { path: 'customer', component: () => import('../components/Customer.vue') },
              { path: 'finder', component: () => import('../components2/FinderShow.vue') },
              { path: 'usercust', component: () => import('../components/UserCust.vue') },
              { path: 'problems', component: () => import('../components/Problems.vue') },
              { path: 'journal', component: () => import('../components/Journal.vue') },
            ]
          },          
        ]

        },
      

      {
        path: 'dct',
        component: () => import('../components/Dct.vue')
      },

      {
        path: 'managment',
        component: () => import('../components/Managment.vue')
      },
      ]
  },

  
  
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
